.id {
  font-size: 12px;
  margin-top: 5px;
}
.checkbox {
  text-align: center;
}
.name {
  font-weight: 500;
  font-size: 15px;
  line-height: 17px;
  color: #2e3b52;
}
.description {
  font-size: 14px;
  line-height: 17px;
  color: #2e3b52;
}
.active {
  background: #ebf2ff !important;
  .checkbox {
    position: relative;
    &:before {
      content: '';
      position: absolute;
      background: #0a65ff;
      width: 4px;
      height: 100%;
      left: 0;
      bottom: 0;
    }
  }
}
.actions {
  position: relative;
  text-align: center;
  padding-right: 15px !important;
  svg {
    fill: #7D90B2;
    cursor: pointer;
    &:hover {
      fill: #000000;
    }
  }
}
.activeWrap {
  display: flex;
  justify-content: space-around;
}
.profit {
  display: block;
}
.value {
  color: #2E3B52;
  font-size: 14px;
}
.currency {
  color: #606F89;
  font-size: 12px;
  margin-top: 5px;
}